@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
*{
    box-sizing: border-box;
    margin:0px; 
}


.Whole-container{
    width:100%;
    height:100vh; 
    background:url(/React_loader/static/media/bg.8d4a71ba.jpg); 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center; 
    color:#fff;
}

.loaderContainer{
    width:300px; 
    height:300px; 
    display: flex;
    justify-content: center;
    align-items: center;
    position:relative;
}

.loaderContainer.grow{
    -webkit-animation: grow 3s linear forwards;
            animation: grow 3s linear forwards;
}
.loaderContainer.shrink{
    -webkit-animation: shrink 3s linear forwards;
            animation: shrink 3s linear forwards;
}

.grad-circle{
    width:320px; 
    height:320px; 
    position: absolute;
    top:-10px;
    left:-10px; 
    border-radius: 50%;
    background:conic-gradient(
        #55b7a4 0%,
        #4ca493 40%,
        #fff 40%,
        #fff 60%,
        #336d62 60%,
        #2a5b52 100%
    );
    z-index:0; 
}

.circle{
    width:300px;
    height:300px;
    position: absolute;
    top:0px;
    left:0px; 
    border-radius:50%; 
    background-color:#010f1c; 
}

.pointer-container{
    width:20px; 
    height:190px;
    position: absolute;
    top:-40px; 
    left:140px;
    -webkit-animation:rotate 7.5s linear forwards infinite;
            animation:rotate 7.5s linear forwards infinite;
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;

}

.pointer{
    width:20px; 
    height: 20px;
    border-radius: 50%;
    background-color:#fff;
}

.text{
    z-index:1;
}

h2{
    position: absolute;
    top:20px; 
}

h6{
    position: relative;
    top:-100px;
    left:7px;
}

.on{
    -webkit-animation:loadingGrow 3s linear forwards;
            animation:loadingGrow 3s linear forwards; 
}

.off{
    -webkit-animation:loadingShrink 3s linear forwards;
            animation:loadingShrink 3s linear forwards; 
}

@-webkit-keyframes rotate{
    from{
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg)
    }
    to{
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg)
    }
}

@keyframes rotate{
    from{
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg)
    }
    to{
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg)
    }
}

@-webkit-keyframes grow{
    from{
        -webkit-transform:scale(1);
                transform:scale(1)
    }
    to{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

@keyframes grow{
    from{
        -webkit-transform:scale(1);
                transform:scale(1)
    }
    to{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

@-webkit-keyframes shrink{
    from{
        -webkit-transform:scale(1.2);
                transform:scale(1.2)
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes shrink{
    from{
        -webkit-transform:scale(1.2);
                transform:scale(1.2)
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@-webkit-keyframes loadingGrow{
    from{
        -webkit-transform:scale(1);
                transform:scale(1)
    }
    to{
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
}

@keyframes loadingGrow{
    from{
        -webkit-transform:scale(1);
                transform:scale(1)
    }
    to{
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
}

@-webkit-keyframes loadingShrink{
    from{
        -webkit-transform:scale(1.3);
                transform:scale(1.3)
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes loadingShrink{
    from{
        -webkit-transform:scale(1.3);
                transform:scale(1.3)
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
