.Whole-container{
    width:100%;
    height:100vh; 
    background:url('./bg.jpg'); 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center; 
    color:#fff;
}

.loaderContainer{
    width:300px; 
    height:300px; 
    display: flex;
    justify-content: center;
    align-items: center;
    position:relative;
}

.loaderContainer.grow{
    animation: grow 3s linear forwards;
}
.loaderContainer.shrink{
    animation: shrink 3s linear forwards;
}

.grad-circle{
    width:320px; 
    height:320px; 
    position: absolute;
    top:-10px;
    left:-10px; 
    border-radius: 50%;
    background:conic-gradient(
        #55b7a4 0%,
        #4ca493 40%,
        #fff 40%,
        #fff 60%,
        #336d62 60%,
        #2a5b52 100%
    );
    z-index:0; 
}

.circle{
    width:300px;
    height:300px;
    position: absolute;
    top:0px;
    left:0px; 
    border-radius:50%; 
    background-color:#010f1c; 
}

.pointer-container{
    width:20px; 
    height:190px;
    position: absolute;
    top:-40px; 
    left:140px;
    animation:rotate 7.5s linear forwards infinite;
    transform-origin: bottom center;

}

.pointer{
    width:20px; 
    height: 20px;
    border-radius: 50%;
    background-color:#fff;
}

.text{
    z-index:1;
}

h2{
    position: absolute;
    top:20px; 
}

h6{
    position: relative;
    top:-100px;
    left:7px;
}

.on{
    animation:loadingGrow 3s linear forwards; 
}

.off{
    animation:loadingShrink 3s linear forwards; 
}

@keyframes rotate{
    from{
        transform:rotate(0deg)
    }
    to{
        transform:rotate(360deg)
    }
}

@keyframes grow{
    from{
        transform:scale(1)
    }
    to{
        transform: scale(1.2);
    }
}

@keyframes shrink{
    from{
        transform:scale(1.2)
    }
    to{
        transform: scale(1);
    }
}

@keyframes loadingGrow{
    from{
        transform:scale(1)
    }
    to{
        transform: scale(1.3);
    }
}

@keyframes loadingShrink{
    from{
        transform:scale(1.3)
    }
    to{
        transform: scale(1);
    }
}